import 'parsleyjs';
import dayjs from 'dayjs';
import customParseFormat from 'dayjs/plugin/customParseFormat';
import { PAGE_ENTER, PAGE_LEAVE } from './constants';

dayjs.extend(customParseFormat);

window.Parsley.addValidator('requiredIfChecked', {
    requirementType: 'string',
    validateString: function(value, requirement) {
        const checkbox = document.querySelector(requirement);

        if (!checkbox) {
            return false;
        }

        if (checkbox.checked && !value.trim()) {
            return false;
        }

        return true;
    },
    messages: {
        en: 'Required field',
        ru: ' '
    },
    priority: 33
});

window.Parsley.addValidator('phone', {
    requirementType: 'string',
    validateString: function(value) {
        if (value.trim() === '') return true;
        return /^(\+7|7|8)?[\s\-]?\(?[489][0-9]{2}\)?[\s\-]?[0-9]{3}[\s\-]?[0-9]{2}[\s\-]?[0-9]{2}$/.test(value);
    },
    messages: {
        en: 'This value should be a mobile number',
        ru: '    '
    }
});

window.Parsley.addValidator('date', {
    requirementType: 'string',
    validateString: function(value) {
        if (value.trim() === '') return true;
        return dayjs(value, 'DD.MM.YYYY', true).isValid();
    },
    messages: {
        en: 'Enter correct date',
        ru: '  '
    }
});

Parsley.addMessages('ru', {
    defaultMessage: ' .',
    type: {
        email: '      E-mail',
        url: '   .',
        number: ' .',
        integer: '  .',
        digits: '  .',
        alphanum: ' - .'
    },
    notblank: '    .',
    required: ' ',
    pattern: '  .',
    min: '       %s.',
    max: '       %s.',
    range: '     %s  %s.',
    minlength: '      %s .',
    maxlength: '      %s .',
    length: '     %s  %s .',
    mincheck: '   %s .',
    maxcheck: '   %s .',
    check: '  %s  %s .',
    equalto: '   .'
});

Parsley.setLocale('ru');

export default function validation() {
    let instances = [];
    function initializeValidation(context = document) {
        const formsToValidate = Array.from(context.querySelectorAll('form[data-need-validation]'));

        formsToValidate.forEach(form => {
            const instance = $(form).parsley();
            instances.push(instance);
        });
    }

    function destroyValidation() {
        instances.forEach(instance => {
            instance.destroy();
        });
        instances = [];
    }

    initializeValidation();

    document.addEventListener(PAGE_LEAVE, event => {
        destroyValidation();
    });

    document.addEventListener(PAGE_ENTER, event => {
        initializeValidation(event.detail.container);
    });
}
